/* Position and sizing of burger button */
.bm-burger-button {
    display: none;
    position:sticky;
    width: 36px;
    height: 30px;
    right: 36px;
    top: 36px;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: #373a47;
  }
  
  /* Color/shape of burger icon bars on hover*/
  .bm-burger-bars-hover {
    background: #a90000;
  }
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  /* Color/shape of close button cross */
  .bm-cross {
    background: #bdc3c7;
  }
  
  /*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
  .bm-menu-wrap {
    position: fixed !important;
    height: 100% !important;
    top: 0;
    z-index: 999999 !important;
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: #1a242ded;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #373a47;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: #b8b7ad;
    padding: 0.8em;
  }
  
  /* Individual item */
  .bm-item {
    display: inline-block;
  }
  
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
  }


  
  .bm-burger-bars {
    background: white;
  }
  
  
  .bg_two_contacts a{
    padding: 2px 0;
  }
  .bm-item-list a {
    
    color: white;
    text-decoration: none;
  }
  
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
  }
  
  /* Стиль поиска */
  .menu-search input {
    width: 100%;
    padding: 8px;
    margin-bottom: 15px;
  }
  
  /* Стиль кнопок выбора языка */

  .menu_item {
    padding: 10px 15px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2); /* Красивый полупрозрачный бордер */
    transition: background-color 0.3s;
  }
  
  .menu_item:hover {
    background-color: rgba(255, 255, 255, 0.1); /* Лёгкое затемнение при наведении */
  }
  
  .menu_item:last-child {
    border-bottom: none; /* Убираем бордер снизу у последнего элемента */
  }
  
  .search_input {
    padding: 8px;
    border-radius: 6px;
    border: 1px solid rgba(255, 255, 255, 0.3);
    background-color: #1a242d;
    color: white;
    width: 100%;
  }
  
  .search_input::placeholder {
    color: rgba(255, 255, 255, 0.6);
  }
  